body, .form_wrap .btn.submit, .person_card .person_group, .person_card .person_position:after {
  background-color: #48d1a0;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  color: #6a6f8c;
  font: 600 16px/18px 'Open Sans',sans-serif;
}

*, :after, :before {
  box-sizing: border-box;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.one-half {
  width: 50%;
  float: left;
  box-sizing: border-box;
}

.svg-loader {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  margin-left: -25px;
  margin-right: -25px;
  display: block;
}

.svg-loader #spinner {
  fill: #48d1a0;
}

.form_wrap {
  margin: 50px auto;
  position: relative;
  width: 300px;
  min-height: 345px;
  padding: 25px 30px;
  border-radius: 5px;
  background: #fff;
  box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.14), 0 17px 50px 0 rgba(0, 0, 0, 0.05);
}

.form_wrap .logo {
  width: 60%;
  display: block;
  margin: 0px auto 25px;
}

.form_wrap .logo .text {
  fill: #6f6f6f;
}

.form_wrap .logo .text {
  fill: #6f6f6f;
}

.form_wrap .logo .pi {
  fill: #48d1a0;
}

.form_wrap .form_group {
  margin-bottom: 25px;
}

.form_wrap .form_group:after, .form_wrap .form_group:before {
  content: "";
  display: table;
}

.form_wrap .form_group:after {
  clear: both;
}

.form_wrap .form_group input {
  outline: none;
}

.form_wrap .form_group .form_alert {
  color: #c91518;
  font-size: 0.9em;
  margin-top: 20px;
  text-align: center;
}

.form_wrap .form_group .form_label {
  color: #aaa;
  text-transform: uppercase;
  font-size: 0.8em;
  display: block;
  letter-spacing: 1px;
}

.form_wrap .form_group .form_input {
  display: block;
  font-size: 0.9em;
  width: 100%;
  letter-spacing: 1px;
  border: 0;
  border-bottom: 2px solid rgba(170, 170, 170, 0.6);
  padding: 11px;
  color: #6f6f6f;
  transition: border .5s ease-in-out;
}

.form_wrap .form_group .form_input:focus {
  border: 0;
  border-bottom: 2px solid #48d1a0;
}

.form_wrap .form_group input[type="password"] {
  color: #6f6f6f;
  font: small-caption;
  letter-spacing: 3px;
  font-size: 16px;
}

.form_wrap .btns {
  margin-top: 55px;
  margin-bottom: 10px;
}

.form_wrap .btn {
  font-size: 0.8em;
  display: inline-block;
  border: none;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 1px;
  box-shadow: 0 4px 23px -8px rgba(171, 171, 171, 0.7);
}

.form_wrap .btn:focus {
  outline: none;
}

.form_wrap .btn.btn-rounded {
  padding: 9px 26px;
  -webkit-border-radius: 30px;
  -webkit-background-clip: padding-box;
  -moz-border-radius: 30px;
  -moz-background-clip: padding;
  border-radius: 30px;
  background-clip: padding-box;
}

.form_wrap .btn.submit {
  padding: 11px 28px;
  border: none;
  transition: box-shadow .2s ease-in-out;
}

.form_wrap .btn.submit:hover {
  box-shadow: 0px 0px 20px 0px rgba(72, 209, 160, 0.5);
}

.form_wrap .btn.cancel {
  background: none;
  color: rgba(170, 170, 170, 0.6);
  border: 2px solid rgba(170, 170, 170, 0.6);
}

.form_wrap .btn.cancel:hover {
  box-shadow: 0px 0px 20px 0px rgba(170, 170, 170, 0.1);
  transition: box-shadow .2s ease-in-out, border .2s ease-in-out, color .2s ease-in-out;
  color: #aaa;
  border: 2px solid #aaaaaa;
}

.person_card {
  text-align: center;
}

.person_card .person_img img, .person_card .person_img canvas {
  border-radius: 50%;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
  margin: 0 auto;
  display: block;
  width: 128px;
  height: 128px;
}

.person_card .person_name,
.person_card .person_position,
.person_card .person_inst {
  text-align: center;
  color: #6f6f6f;
}

.person_card .person_name {
  margin: 20px 0 15px;
  font-weight: bold;
  font-size: 1.1em;
}

.person_card .person_inst {
  margin: 20px 0;
  font-size: 1em;
}

.person_card .person_group {
  font-size: 1em;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 1em;
}

.person_card .person_position {
  font-size: 1em;
}

.person_card .person_position:after {
  content: "";
  display: block;
  width: 15%;
  margin: 15px auto 0;
  height: 3px;
}

.person_card .person_struct_item {
  width: 100%;
}

.smart-crop-container {
  position: relative;
  overflow: hidden;
}

.person_card .person_img img {
  visibility: hidden;
}
